export function tabs() {
    const $tabsTriggers = $(".js-tabs-triggers");

    $tabsTriggers.each((index, item) => {
        const indexTab = $(item).find(".is-active").parent().index();
        $(item).siblings(".js-tabs-content")
            .eq(indexTab)
            .addClass("is-active");
    });

    $tabsTriggers.find("a").on("click", function (e) {
        e.preventDefault();

        const $self = $(this);
        const $container = $self.closest(".js-tabs-triggers");
        const $triggers = $container.find("a");
        const index = $self.parent().index();
        const $content = $container.siblings(".js-tabs-content");
        const curContent = $content.eq(index);

        $triggers.removeClass("is-active");
        $self.addClass("is-active");

        $content.removeClass("is-active");
        curContent.addClass("is-active");
    });
}


