// init header navigation
export function headerNavigation() {
    $(".js-header-menu").find(".menu-item-has-children > a").on("click", (e) => {
        const isTabletRes = window.innerWidth < this.desktop;

        e.preventDefault();

        if (isTabletRes) {
            const $container = $(e.target).closest(".menu-item");
            const $content = $(e.target).siblings(".js-header-menu-content");

            $container.toggleClass("is-open")
                .siblings(".menu-item")
                .removeClass("is-open");

            $(".js-header-menu-content").not($content).slideUp();
            $content.slideToggle();
        }
    });
}

export function burgerTrigger() {
    document.querySelector(".js-burger").addEventListener("click", (e) => {
        let burgerContainer = e.target.closest(".js-burger-container");
        let body = document.querySelector("body");

        burgerContainer.classList.toggle("is-open");
        body.classList.toggle("is-locked");

        e.preventDefault();
    });
}


