
// these properties will be available from anywhere via this.property
export const constants = {
    isTouch: "ontouchstart" in window ? function () {document.body.classList.add("touch"); return true;}() : function () {document.body.classList.add("no-touch"); return false;}(),
    body: $("body"),
    tablet: 768,
    tabletLandscape: 1024,
    desktop: 1200
};

export function iframeReload() {
    const eventMethod = window.addEventListener
        ? "addEventListener"
        : "attachEvent";
    const eventer = window[eventMethod];
    const messageEvent = eventMethod === "attachEvent"
        ? "onmessage"
        : "message";


    eventer(messageEvent, function (e) {
        if (e.data === "formIsSent" || e.message === "formIsSent") {
            setTimeout(() => {
                [...document.querySelectorAll(".js-subscribe-frame iframe")].forEach(item => {
                    item.src = item.src;
                });
            }, 3000);
        }
    });
}


