export function slider() {
    const options = {
        dots: false,
        arrows: true,
        draggable: false,
        nextArrow: "<button class='slick-next slick-arrow'><svg class='slick-arrow-icon'><use xlink:href='#arrow-right'></use></svg></button>",
        prevArrow: "<button class='slick-prev slick-arrow'><svg class='slick-arrow-icon'><use xlink:href='#arrow-right'></use></svg></button>",
    };

    $(".js-slider").on("init", function (event, slick) {
        $(slick.$slider).addClass("is-initialized");
    });

    $(window).on("load", () => $(".js-slider").slick(options));
}


