import { tabs } from "./modules/tabs";
import { slider } from "./modules/slider";
import { closeCookiePopup } from "./modules/cookie";

binder({
    bounds: {
        "html": constants,
        ".js-header-menu": headerNavigation,
        ".js-burger": burgerTrigger,
        ".js-tabs-triggers": tabs,
        ".js-slider": slider,
        ".js-subscribe-frame": iframeReload,
        ".js-cookie": closeCookiePopup
    },
    runTests: false
});



