export function closeCookiePopup() {
    const $cookiePopup = $('.js-cookie');
    const isCookieAccepted = localStorage.getItem('cookieAccepted');
    if (isCookieAccepted) {
        $cookiePopup.addClass('is-accepted');
    }
    $cookiePopup.addClass('is-loaded');
    $('.js-close-cookie').on('click', e => {
        e.preventDefault();
        localStorage.setItem('cookieAccepted', 'true');
        $(e.target).closest($cookiePopup).stop().animate({'height': 0}, 500, () => {
            $(e.target).closest($cookiePopup).css('display', 'none');
        });
    });
}


